@import url('https://fonts.googleapis.com/css2?family=Fredoka:wght@300..700&display=swap');


body{
  background: linear-gradient(90deg,#525252,#C6B677,#86A59C,#F5FFC6);
  background-size: 300% 300%;
  animation: color 12s ease-in-out infinite;
}

@keyframes color{
  0%{
    background-position: 0 50%;
  }
  50%{
    background-position: 100% 50%;
  }
  100%{
    background-position: 0 50%;
  }
}

.to-do-list{
  font-family: "Fredoka", sans-serif;
  text-align: center;
  margin-top: 100px;
}
.to-do-list img{
  width: 60px;
  height: auto;
}
h1{
  font-size: 4rem;
  color: white;
}
button{
  font-size: 1.7rem;
  font-family: "Fredoka", sans-serif;
  padding: 10px 20px;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.5s ease;
  background-color: #FCECC9;
}
.add-button{
  background-color: hsl(74, 43%, 54%);
  transition: all 0.3s ease;
}
.add-button:hover{
  transform: translateY(-5px);
  box-shadow: 0 12px 16px 0 rgba(230, 228, 228, 0.24), 0 17px 50px 0 rgba(0,0,0,0.19); 
}
.add-button:active{
  transform: translateY(-2x);
  box-shadow: 0 12px 16px 0 rgba(230, 228, 228, 0.24), 0 17px 50px 0 rgba(0,0,0,0.19); 
}

.delete-button img{
  transition: all 0.3s ease;
  height: 42px;
  width: 40px;
}
.delete-button img:hover{
  transform: translateY(-5px);
}
.delete-button img:active{
  transform: translateY(-2x);
}

.move-button img{
  transition: all 0.3s ease;
  height: 42px;
  width: 40px;
}
.move-button img:hover{
  transform: translateY(-5px);
}
.move-button img:active{
  transform: translateY(-2x);
}

input[type="text"]{
  font-size: 1.6rem;
  padding: 10px;
  border: 2px solid hsla(0, 0%, 80%, 0.5);
  border-radius: 5px;
  color: hsla(0, 0%, 0%, 0.933);
  font-family: "Fredoka", sans-serif;
  width: 20%;
}
ol{
  padding: 0;
}
li{
  font-size: 2rem;
  font-weight: bold;
  padding: 15px;
  background-color: #FCECC9;
  margin-bottom: 10px;
  border: 3px solid hsla(0, 0%, 85%, 0.75);
  border-radius: 10px;
  display: flex;
  align-items: center;
  width: 50%;
  margin-left: 470px; 
}
.text{
  flex: 1;
}
.delete-button, .move-button{
  padding: 8px 12px;
  font-size: 1.4rem;
  margin-left: 10px;
}
 
